@use '../../utility/variables' as v;

.popup-blocker{
    position: absolute;
    background-color: rgba(128, 128, 128, 0);
    width: 100%;
    height: 100vh;
    z-index: 9999;
    display: flex;
    justify-content: center;

    .bill-popup{
        margin-top: 10vh;
        background-color: rgb(250, 250, 250);
        border-radius: 3%;
        border: 1px solid black;
        width: 90%;
        height: 65vh;
    
        display: flex;
        flex-direction: column;
        align-items: center;

        .header{
            font-size: 18px;
            font-weight: bold;
            color: v.$main-color;
            margin-top: 1vh;
        }

        .bills-container{
            width: 97%;
            margin: 3%;
            display: flex;
            flex-direction: column;
            align-items: center;
            overflow: scroll;

            .bill-container{
                border: 2px solid v.$main-color;
                border-radius: 5%;
                margin-bottom: 3%;
                width: 96%;
                display: flex;
                align-items: center;
                flex-direction: column;
                .bill-top-container{
                    width: 100%;
                    display: flex;
                    justify-content: space-around;
                    margin-top: 3%;

                    .bill-icon-container{
                        margin-left: 3%;
                        flex:1;
                        background-color: yellow;
                        border-radius: 50%;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        .bill-icon{
                            font-size: 4rem;
                        }
                    }
                    .top-text-container{
                        flex:3;
                        display: flex;
                        flex-direction: column;
                        margin-left: 4%;

                        .bill-text{
                            font-weight: bold;
                        }

                        .bill-name{
                            font-size: 0.9rem;
                            color: deepskyblue;
                        }
                    }
                }
                .bill-bottom-container{
                    margin: 3%;
                    .address{
                        font-size: 13px;
                    }
                }
            }
        }
        .button{
            font-weight: bold;
            width: 90%;
            height: 5vh;
            color: white;
    
            border-radius: 5%;
    
            box-shadow: 0px 0px 5px 2px #000000;
        }
        .close-button{
            background-color: red;
            margin-bottom: 2vh;
        }
    }
}